<template>

<div class="container" @keyup='nextItem'>
    <CustomCursor v-bind:class="{ 'fantasy-book': book.fantasyBookBoolean, 'romance-book': book.romanceBookBoolean, 'adventure-book': book.adventureBookBoolean, 'history-book': book.historyBookBoolean }"/>
    <Header v-bind:class="{ 'fantasy-book': book.fantasyBookBoolean, 'romance-book': book.romanceBookBoolean, 'adventure-book': book.adventureBookBoolean, 'history-book': book.historyBookBoolean }"/>
    <BackButton v-bind:class="{ 'fantasy-book': book.fantasyBookBoolean, 'romance-book': book.romanceBookBoolean, 'adventure-book': book.adventureBookBoolean, 'history-book': book.historyBookBoolean }"/>
    <LoadingQuote :v-if="loadingCount<2" class="loading-screen" :loadingprop="isLoading" :loadingcountprop="loadingCount" :fadedprop="fadedQuote" :booktitleprop="book.title" :bookquoteprop="book.quote" v-bind:class="{ 'fantasy-book': book.fantasyBookBoolean, 'romance-book': book.romanceBookBoolean, 'adventure-book': book.adventureBookBoolean, 'history-book': book.historyBookBoolean }"></LoadingQuote>

    <div class="bookpage">

    <!--BG Image-->
    <div class="bp-image-background" :alt="book.adaptations[selectedIndex].name" v-bind:style="{ backgroundImage: `url(${book.adaptations[selectedIndex].image})`}" ></div>

    <!--Book-->
    <BookItem ref="bookAnimation" :selectedindexprop="book.selectedFilm" :slugprop="slug" :titleprop="book.title" :authorprop="book.author" :themeprop="book.themes" :numberprop="book.number" v-bind:class="{ 'bookslide': fadedQuote, 'fantasy-book': book.fantasyBookBoolean, 'romance-book': book.romanceBookBoolean, 'adventure-book': book.adventureBookBoolean, 'history-book': book.historyBookBoolean }"></BookItem>

    <!--Book Info Panel-->
    <div class="book-summary-item book-panel-item" v-bind:class="{ 'fantasy-book': book.fantasyBookBoolean, 'romance-book': book.romanceBookBoolean, 'adventure-book': book.adventureBookBoolean, 'history-book': book.historyBookBoolean}">
            <div class="book-summary-title panel-section" @click="expandedSummary = !expandedSummary">
                <span class="panel-title">Book Summary</span>
                <span class="expand" v-if="expandedSummary === false">Expand</span>
                <span class="expand" v-if="expandedSummary === true">Close</span>
            </div>
            <div class="book-summary-text" v-if="expandedSummary === true">
                <div class="book-details">
                    <br>
                    <div class="book-title"> {{book.title}}</div>
                    <div class="sans"> by {{book.author}}</div>
                    <div class="sans"> {{book.year}}</div>
                </div>
                <br>
                <div> {{book.summary}} </div>
        
            <br>
            <div class="book-downloads-links">
                <a :href="'/books/pdf/'+(book.slug) + '.pdf'" :download="book.title + 'by' + book.author" class="pointer-link">PDF</a>
                <a :href="'/books/html/' + (book.slug) + '.html'" :download="book.title + 'by' + book.author" class="pointer-link">HTML Text</a>
                <!-- <a href="link.jpg" :download="book.title" class="pointer-link">EPUB</a> -->
            </div>

            </div>
    </div>


        <!--Movie Info Panel-->
        <div class="book-adaptation-item book-panel-item" v-bind:class="{ 'fantasy-book': book.fantasyBookBoolean, 'romance-book': book.romanceBookBoolean, 'adventure-book': book.adventureBookBoolean, 'history-book': book.historyBookBoolean}">
            <div class="book-adaptation-title panel-section pointer-link" @click="expandedAdaptations = !expandedAdaptations">
                    <span class="panel-title">Films and Adaptations</span>
                    <span class="expand" v-if="expandedAdaptations === false">Expand</span>
                    <span class="expand" v-if="expandedAdaptations === true">Close</span>   
            </div>

            <div class="book-adaptations-wrapper" v-if="expandedAdaptations === true" @mouseover="imageColor = true" @mouseleave="imageColor = false">
                <br>
                <div class="book-adaptations-text pointer-link" v-for="(adaptation, index) in book.adaptations" :key="adaptation.name + adaptation.year"  @click="adaptation.selectedMovie = !adaptation.selectedMovie; selectMovie(index)" :class="[selectedIndex === index ? 'selected-movie' : '']">
                    <div class="movie-line">
                        <div class="adaptation-title"> {{adaptation.name}}</div>
                        <div class="adaptation-year">{{adaptation.year}}</div>
                    </div>

                        <img class="hide" :src="adaptation.image"/>
                </div>
            </div>
            

        </div>

    </div>
</div>
</template>


<script>
import store from '@/store'

import BookItem from '@/components/BookItem.vue';
import BackButton from '@/components/layout/BackButton.vue';
import LoadingQuote from '@/components/LoadingQuote.vue';
import Header from '@/components/layout/Header.vue';
import CustomCursor from '@/components/layout/CustomCursor.vue';




export default {



    name: "BookPage",
    components: {
        BookItem,
        BackButton,
        LoadingQuote,
        Header,
        CustomCursor,
    
    },
    data(){

        return{
            expandedSummary: true,
            expandedDownloads: true,
            expandedAdaptations: true,
            selectedMovie: false,
            selectedIndex: 0,
            fileName: this.slug,
            isLoading:true,
            fadedQuote:false,
            loadingCount:0,
        }
    },
    props:{
        slug: {
            type: String,
            required: true
        },
        adaptation: {
            type: Object,
        },
      
    },
  
    methods: {
          
        nextItem(){
            if (event.keyCode == 38 && this.selectedIndex > 0) {
            this.selectedIndex--
        } else if (event.keyCode == 40 && this.selectedIndex < this.book.adaptations.length - 1) {
            this.selectedIndex++
      }},
        onLoad: function(){
            console.log("call on load");
            this.loadingCount = this.loadingCount + 1;
            this.isLoading = false;
        },
             selectMovie(index){
                this.selectedIndex = index;
          },
    },
    computed: {
        book(){
            return store.books.find(
                book => book.slug === this.slug
            )},
        
    
    },
    created(){
        this.onLoad();
    },
    mounted() {

        //   setTimeout(() => {
        //     this.isLoading = false
        //   }, 5000)

        this.selectedIndex = this.book.selectedFilm;


        document.addEventListener("keyup", this.nextItem);

    }
}  

</script>


